import { fireDatabase, fireStorage } from 'myFirebase';
import React, { useState } from 'react';
import { doc, deleteDoc, updateDoc } from "firebase/firestore";
import { deleteObject, ref } from "firebase/storage";
import style from 'css/Content.module.css';

function Content({content, isWriter}) {
  const [updateMode, setUpdateMode] = useState(false);
  const [editText, setEditText] = useState(content.text);

  const contentFromDatabase = doc(fireDatabase, "contents", `${content.id}`);

  const deleteButtonHandler = async() => {
    const confirm = window.confirm("ì •ë§ ì‚­ì œí•˜ì‹œê² ìŠµë‹ˆê¹Œ?");
    if(confirm) {
      await deleteDoc(contentFromDatabase);
      if(content.imageFileURL !== "") {
        await deleteObject(ref(fireStorage, content.imageFileURL));
      }
    }
  };

  const toggleUpdate = () => {
    setUpdateMode((current) => !current);
  };

  const editTextChange = ({target : {value}}) => {
    setEditText(value);
  };

  const formSubmit = async(e) => {
    e.preventDefault();
    await updateDoc(contentFromDatabase, {text: editText});
    setUpdateMode(false);
  };

  return (
    <div>
      {updateMode ?
        <> 
          {isWriter && 
            <div className={style.update_form}>
              <form onSubmit={formSubmit}>
                <input type="text" value={editText} onChange={editTextChange} required/>
                <button type="submit">ìˆ˜ì •í•˜ê¸°</button>
              </form>
              <button onClick={toggleUpdate}>ì·¨ì†Œ</button>
            </div>
          }
        </>
        :
        <>
          <h4 className={style.title}>{content.text}</h4>
          {content.imageFileURL && 
            <img className={style.image} src={content.imageFileURL} alt="ì—…ë¡œë“œ ì´ë¯¸ì§€" />
          }
          {/* ì‘ì„±ìì™€ ë¡œê·¸ì¸ ì¤‘ì¸ ì‚¬ìš©ìê°€ ê°™ì„ ë•Œ */}
          {isWriter && 
            <div className={style.content_button}>
              <button type="button" onClick={toggleUpdate}>ìˆ˜ì •</button>
              <button type="button" onClick={deleteButtonHandler}>ì‚­ì œ</button>
            </div>
          }
        </>
        }
    </div>
  )
}

export default Content;
