import { firebaseAuth } from 'myFirebase';
import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { updateProfile } from 'firebase/auth';
import style from 'css/Profile.module.css';

function Profile({userObject, currentUserName}) {
  const [userName, setUserName] = useState(userObject.displayName);
  const navigate = useNavigate();
  const handleLogoutButton = () => {
    firebaseAuth.signOut();
    navigate('/');
  };

  const userNameChange = ({target: {value}}) => {
    setUserName(value);
  };

  const formSubmit = async(e) => {
    e.preventDefault();
    // firebase Authì˜ displayNameì„ ë³€ê²½ì‹œì¼œì¤Œ
    if(userObject.displayName !== userName) {
      await updateProfile(firebaseAuth.currentUser, {
        displayName: userName
      });
      // projectì•ˆì—ì„œì˜ displayName ì—­ì‹œ ë³€ê²½ì‹œì¼œì¤Œ
      currentUserName();
    }
    navigate('/');
  };
  
  return (
    <div>
      <h4>{userObject.displayName}ì˜ Profile</h4>
      <form onSubmit={formSubmit}>
        <input className={style.name_input} type="text" placeholder="ì´ë¦„" value={userName} onChange={userNameChange}/>
        <button className={style.edit_button} type="submit">ìˆ˜ì •í•˜ê¸°</button>
      </form>
      <button className={style.logout_button} onClick={handleLogoutButton}>ë¡œê·¸ì•„ì›ƒ</button>
    </div>
  )
}

export default Profile;
