import React, { useState } from 'react';
import { createUserWithEmailAndPassword, signInWithEmailAndPassword } from 'firebase/auth';
import { firebaseAuth } from 'myFirebase';
import style from 'css/Auth.module.css';

function AuthForm() {
  const [form, setForm] = useState({email : "", password: ""});
  const [createAccount, setCreateAccount] = useState(true);
  const [error, setError] = useState("");

  // const formChange = (e) => {
  //   const {target : {name, value}} = e;
  //   if(name === "email") {
  //     setEmail(value);
  //   } else if(name === "password") {
  //     setPassword(value);
  //   }
  // }
  // ìœ„ ì½”ë“œë¥¼ ë°‘ì— ì½”ë“œì²˜ëŸ¼ ê°„ë‹¨í•˜ê²Œ ë³€ê²½ ê°€ëŠ¥

  const formChange = ({target : {name, value}}) => {
    setForm({...form, [name]:value});
  };

  const formSubmit = async(e) => {
    e.preventDefault();
    try {
      let account;
      if(createAccount) {
        // create account
        account = await createUserWithEmailAndPassword(firebaseAuth, form.email, form.password);
      } else {
        // login
        account = await signInWithEmailAndPassword(firebaseAuth, form.email, form.password);
      }
      console.log(account);
    } catch(error) {
      setError(error.message);
    }
  };

  // create Account true or false => ë²„íŠ¼ ë‚´ìš© ë³€ê²½
  const toggleAccountButton = () => {
    setCreateAccount((current) => !current);
  }

  return (
    <div>
      <form className={style.auth_form} onSubmit={formSubmit}>
        <input type="email" name="email" value={form.email} placeholder="ì´ë©”ì¼" required onChange={formChange} />
        <input type="password" name="password" value={form.password} placeholder="ë¹„ë°€ë²ˆí˜¸" required onChange={formChange} />
        <button type="submit">{createAccount ? "íšŒì›ê°€ì…" : "ë¡œê·¸ì¸"}</button>
        {error && <p>Error : {error}</p>}
      </form>
      <button className={style.toggle_button} onClick={toggleAccountButton}>{createAccount ? "ë¡œê·¸ì¸í•˜ê¸°" : "íšŒì›ê°€ì…í•˜ê¸°"}</button>
    </div>
  )
}

export default AuthForm;
